html, body {
    margin: 0;
    scroll-behavior: smooth;
}

/* Variants of the Roboto font */
@font-face {
    font-family: 'Roboto-Regular';
    src: url('./fonts/Roboto-Regular.ttf') format('truetype');
}
@font-face {
    font-family: 'Roboto-Medium';
    src: url('./fonts/Roboto-Medium.ttf') format('truetype');
}
@font-face {
    font-family: 'Roboto-Bold';
    src: url('./fonts/Roboto-Bold.ttf') format('truetype');
}
@font-face {
    font-family: 'Roboto-Light';
    src: url('./fonts/Roboto-Light.ttf') format('truetype');
}

/* ========== Search ========== */

.search-main-container {
    display: block;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.search-main-helper-text {
    display: flex;
    justify-content: center;
    margin-top: 2rem;
    font-size: 1.25rem;
    color: white;
    font-family: 'Roboto-Light';
    text-align: center;
}

.search-main-error-text {
    display: flex;
    justify-content: center;
    margin-top: 2rem;
    font-size: 1.25rem;
    color: rgb(223, 104, 104);
    font-family: 'Roboto-Light';
}

.search-main-flex-container {
    display: flex;
    justify-content: center;
    margin-top: 1rem;
}

.github-logo-search-main {
    color: white;
    font-size: 120;
    transform: scale(7);
    margin-bottom: 64px;
}

.github-logo-search-main-background {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50.5%, -116%);
    background-color: #191F24;
    width: 168px;
    height: 168px;
    border-radius: 50%;
}

/* ========== Page Not Found ========== */

.page-not-found-text {
    display: flex;
    justify-content: center;
    margin-top: 2rem;
    font-size: 1.75rem;
    color: white;
    font-family: 'Roboto-Light';
}

/* ========== Navbar ========== */

.navbar-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 64px;
    font-family: 'Roboto-Medium';
    background-color: #151a1e;
    border-bottom: 1px solid #34414b;
}

.link {
    color: white;
}

.link:hover {
    cursor: pointer;
    color: #e6e6e6;
}

/* ========== Profile ========== */

.profile-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-height: 95vh;
}

.profile-flex-column {
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
}

.profile-following-count-container {
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
    color: white;
    font-size: 1.5rem;
    font-family: 'Roboto-Light';
    min-width: 70px;
    margin: 0 3rem;
}

.profile-avatar {
    width: 168px;
    height: 168px;
    border-radius: 50%;
    border: 2px solid #495b69;
}

.transition-and-fade-container {
	animation-name: transitionAndFadeContainer;
    animation-duration: 1s;
    animation-fill-mode: forwards;
  	opacity: 0;
}

@keyframes transitionAndFadeContainer {
  from {
    opacity: 1;
  }
  to {
 	opacity: 0;
  }
}

.profile-first-fade-in {
    animation-name: fadeIn;
    animation-duration: 1s;
    animation-delay: 1s;
    animation-fill-mode: forwards;
    opacity: 0;
}

.profile-second-fade-in {
    animation-name: fadeIn;
    animation-duration: 1s;
    animation-delay: 1.5s;
    animation-fill-mode: forwards;
    opacity: 0;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@media only screen and (max-width: 1200px){
    /*Tablets [601px -> 1200px]*/
    .github-logo-search-main {
        transform: scale(6);
        margin-bottom: 56px;
    }

    .github-logo-search-main-background {
        transform: translate(-50.5%, -123.5%);
        width: 144px;
        height: 144px;
    }

    .profile-avatar {
        width: 144px;
        height: 144px;
    }
}

@media only screen and (max-width: 600px){
	/*Big smartphones [426px -> 600px]*/
    .github-logo-search-main {
        transform: scale(5);
        margin-bottom: 48px;
    }

    .github-logo-search-main-background {
        transform: translate(-50.5%, -135%);
        width: 120px;
        height: 120px;
    }

    .profile-avatar {
        width: 120px;
        height: 120px;
    }
}

@media only screen and (max-width: 425px){
