    }

    .github-logo-search-main-background {
        transform: translate(-50.5%, -153%);
        width: 96px;
        height: 96px;
    }

    .profile-avatar {
        width: 96px;
        height: 96px;
    }
}

@media only screen and (max-height: 600px) and (min-width: 1000px){

    .github-logo-search-main {
        transform: scale(5);
        margin-bottom: 40px;
    }

    .github-logo-search-main-background {
