    .github-logo-search-main {
        transform: scale(4);
        margin-bottom: 32px;
    }

    .github-logo-search-main-background {
        transform: translate(-50.5%, -153%);
        width: 72px;
        height: 72px;
    }

    .profile-avatar {
        width: 96px;
        height: 96px;
    }

    .search-main-container {
        top: 70%;
        left: 50%;
        transform: translate(-50%, -70%);
    }
}

@media only screen and (max-height: 400px) and (min-width: 1000px){

    .github-logo-search-main {
        transform: scale(3);
        margin-bottom: 24px;
    }

    .github-logo-search-main-background {
        transform: translate(-50.5%, -153%);
        width: 48px;
        height: 48px;
    }

    .profile-avatar {
        width: 72px;
        height: 72px;
    }

    .search-main-container {
        top: 80%;
        left: 50%;
        transform: translate(-50%, -80%);
    }
}